import React,{useEffect,useState} from 'react'
import './style.css';

const UserDetails = () => {
    const [users,setUsers]=useState([]);

    const getUsers=async()=>{
        const response=await fetch('https://jsonplaceholder.typicode.com/users');
        setUsers(await response.json());
    

    }
    useEffect(() => {
        getUsers();
    },[])
    return (<>
{

    users.map((currentElm) => {
        const{name,username,email,address}=currentElm;
        return(<>
<h1> <span>Name=</span>{name}=<span>=UserName</span>{username}=<span>email</span>{email}  <span>Address:</span>street: {address.street}:city:{address.city}</h1>
        </>

        )

    })
}
        </>
    )
}

export default UserDetails
